@use 'Base/variables' as v;
@use 'Base/mixins' as m;


.registrarInforPersonal {
    background-color: v.$blanco;
    padding: 4rem;
    box-shadow: 10px 10px 8px -3px rgba(0, 0, 0, 0.33);
    .Formulario {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .hidden {
            display: none;
        }
        .informacionPersonal, .informacionAcompaantes, 
        .informacionDispositivos, .informacionAutomovil{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .form-group{
                @include m.tablet{
                    max-width: 40rem;
                    input, select{
                        min-width: 0;
                    }
                    select{
                        min-height: 4rem;
                    }
                }
            }
        }
        .personal__titulo,
        .acompaantes__titulo,
        .dispositivos__titulo,
        .automovil__titulo {
            margin-top: 4rem;
            font-size: 2rem;
        }
        .form-check-input {
            min-width: 2rem;
            @include m.tablet {
                min-width: 3rem;
            }
        }
        input,
        label,
        select {
            min-width: 30rem;
            padding: 1rem;
            @include m.tablet {
                min-width: 50rem;
            }
        }
        .confirm__btn {
            @include m.button(v.$secundario, v.$secundario-hover, v.$blanco, v.$blanco-hover);
        }
    }


}