@use 'Base/variables' as v;
@use 'Base/mixins' as m;

body{
    background-color: v.$bg-color;
}
//Ajustando tamao de cabeceras a contenido
.content {
    .title-example{
        margin-top: 3rem;
        margin-bottom: 3rem;
    }
    .__Tabla {
        .tabla {
            .tabla__cabecera{
                tr{
                    #CupoMax{
                        width: 23rem;
                    }
                    .cabecera{
                        width: fit-content;
                        padding: 1rem;
                        text-align: center;
                        vertical-align: middle;
                    }
                }
            }
            .tabla__cuerpo {
                tr {
                    .id,
                    .nombreSala,
                    .estado,
                    .cupoMax,
                    .numeroS,
                    .pisoS {
                        text-align: center;
                        vertical-align: middle;
                        padding: 1rem 4rem 1rem 4rem;
                    }
                }
            }
        }
    }
    h1{
        color:v.$txt-azul;
     }
    .crearS {
        @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
    }
}


