@use 'Base/variables' as v;
@use 'Base/mixins' as m;


.cSala{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh; 
    width: 90%;
    margin-left: 2%;
    @include m.tablet{
        margin-left: 8rem;
    }
    .cSalaHero{
        justify-content: center;
        align-items: center;
        border-radius: 2%;
        background-color: v.$blanco;
        width: 90%;
        box-shadow: 10px 10px 25px -4px rgba(139, 139, 139, 0.75);
        @include m.tablet{
            width: 500px;
            flex-direction: column;
        }
        h1{
            padding-top:2rem;
            text-align: center;
        }
    }
    .cSalaHeroForm{
        padding: 2rem;
        &__container{
            &__label{
                font-size: 1.8rem;
            }
            &__input{
                border: 1.5px solid #ecedec;
                border-radius: 10px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-left: 10px;
                transition: 0.2s ease-in-out;
                margin-bottom: 2rem;
                font-size: 1.4rem;
                width: 100%;
            }
            &__input:focus{
                outline: none;
                border-color: v.$secundario;
            }
            select{
                border: 1.5px solid #ecedec;
                border-radius: 10px;
                height: 40px;
                display: flex;
                align-items: center;
                transition: 0.2s ease-in-out;
                margin-bottom: 2rem;
                font-size: 1.4rem;
                width: 100%;
            }
            select:focus{
                outline: none;
                border-color: v.$secundario;
            }
            select option:hover {
                background-color: rgb(240, 240, 197); 
                color: #000;
            }
            select option:focus {
                background-color: rgb(250, 255, 189); 
                color: #000;
            }
        }
                
        &__button-Container{
            @include m.tablet{
              display: flex;
              justify-content: space-between;
              gap: 2rem;
              max-width: 50rem;
            } 
            .Boton{
              @include m.tablet{
                width: 50%;
              }
              .cSalaHeroForm__button-Container__button__Primario{
                @include m.button(v.$secundario,v.$secundario-hover,v.$negro,v.$blanco);
              }
              .w-100{
                width: 100%;
              }
            }
            &__button{
              &__Secundario{
                  @include m.button(v.$primario,v.$primario-hover,v.$blanco,v.$blanco);
              }
            }
        }
    }   
}